import React, {Component} from 'react'

import {Grid, Icon, List,Label, Card,Table} from "semantic-ui-react"

import * as d3 from 'd3'
import {sankey as d3Sankey,sankeyLinkHorizontal} from 'd3-sankey'

import "../sankey/sankey.css"

export default class sankey_card extends Component{
   
    componentDidMount(){
        const dealer_id = this.props.dealer_id
        const data_base = this.props.data
        const dealer_node = [{"node":0 , "name":dealer_id}]
        const topic_set = new Set()
    
        data_base.forEach(d =>{
            topic_set.add(d.topic)
        })
        const topic_arr = Array.from(topic_set)

        const nodes_arr = dealer_node.concat(topic_arr.map((e,i) =>{
            return {"node":i+1, "name":e}
        }))

        const links_arr = []
        data_base.forEach(d1 => {
            // console.log(nodes.find(o => o.name === d1.topic.toString()))
           links_arr.push({"source":0,
                        "target": topic_arr.indexOf(d1.topic)+1,
                        "type":d1.type,
                        "value":parseFloat(d1.weight)
                    })
        });
        const data = {"nodes":nodes_arr, "links":links_arr}
        

        let svg = d3.select("#sankey_"+dealer_id).append("svg")
            .attr('width', '100%')
            .attr('height', '100%');

        var width = 250;
        var height = 1400;

        let formatNumber = d3.format(",.0f"), format = function(d) {
            return formatNumber(d) + " ";
            }, color = d3.scaleOrdinal(d3.schemeCategory10);
                
        let sankey = d3Sankey()
            .nodeWidth(15)
            .nodePadding(10)
            .extent([ [ 1, 1 ], [ width - 1, height - 5 ] ])
                
        let link = svg.append("g").attr("class", "links").attr("fill", "none")
                .attr("stroke", "#000").attr("stroke-opacity", 0.2).selectAll(
                "path")
                
                
        let node = svg.append("g").attr("class", "nodes").attr("font-family",
                "sans-serif").attr("font-size", 10).selectAll("g");
                
        let graph = sankey(data);
                
            link = link.data(data.links).enter().append("path").attr("d",
                sankeyLinkHorizontal()).attr("stroke-width", function(d) {
                return Math.max(1, d.width);
                });
                
                link.append("title").text(
                function(d) {
                return d.source.name + " -> " + d.target.name + "\n"
                        +"value :" +d.value;
                });
                
                node = node.data(data.nodes)
                .enter()
                .append("g")
                .call(
                    d3.drag().subject(function(d) {
                        return d
                    }).on('start', function() {
                        this.parentNode.appendChild(this);
                    })
                .on('drag', dragmove));
                
                node.append("rect").attr("x", function(d) {
                return d.x0;
                }).attr("y", function(d) {
                return d.y0;
                }).attr("height", function(d) {
                return d.y1 - d.y0;
                }).attr("width", function(d) {
                return d.x1 - d.x0;
                }).attr("fill", function(d) {
                return color(d.name.replace(/ .*/, ""));
                }).attr("stroke", "#000");
                
                node.append("text").attr("x", function(d) {
                return d.x0 - 6;
                }).attr("y", function(d) {
                return (d.y1 + d.y0) / 2;
                }).attr("dy", "0.35em").attr("text-anchor", "end").text(function(d) {
                return d.name;
                })
                //.filter(function(d) {return d.x0 < width / 2;})
                .attr("x", function(d) {
                return d.x1 + 6;
                }).attr("text-anchor", "start");
                
                node.append("title").text(function(d) {
                return d.name + "\n" + format(d.value);
                });
                
                
                // the function for moving the nodes
                function dragmove(d) {
                
                var rectY = d3.select(this).select("rect").attr("y");
                
                d.y0 = d.y0 + d3.event.dy;
                
                var yTranslate = d.y0 - rectY;
                
                d3.select(this).attr("transform", "translate(0" + "," + (yTranslate) + ")");
                
                sankey.update(graph);
                link.attr("d", sankeyLinkHorizontal());
                }
        
    }

    render(){

        function groupBy(objectArray, property) {
            return objectArray.reduce(function (acc, obj) {
              let key = obj[property]
              if (!acc[key]) {
                acc[key] = []
              }
              acc[key].push(obj)
              return acc
            }, {})
        }

        const dealer_id = this.props.dealer_id
        const data = this.props.data
        const topics = [...new Set(data.map(item => item.topic))];
        const topic_groups = Object.entries(groupBy(data, 'topic'))
        const type_groups = Object.entries(groupBy(data,'type'))

        const topic_details_unsorted = topic_groups.map((e,i)=>{
            return(
                {"topic":e[0],
                 "edges":e[1].length,
                 "type": groupBy(e[1],'type')
                }
            )
        })
        const topic_details = topic_details_unsorted.sort((a,b) => (a.edges < b.edges) ? 1 : ((b.edges < a.edges) ? -1 : 0));
        
        
        return(
            <Grid columns='equal'>
                <Grid.Column width={8}>
                <Card fluid>
                    <Card.Content header={"Dealer ID : "+dealer_id+" to Topics"} />
                    <Card.Content id={"sankey_"+dealer_id} style={{height:"1500px"}}>
                    </Card.Content>
                </Card>
                </Grid.Column>
                <Grid.Column width={8}>
                    <Card fluid>
                        <Card.Content>
                            <Card.Header>Summary</Card.Header>
                            <Card.Meta>Dealer ID: {dealer_id}</Card.Meta>
                    
                            <Card.Description>
                                <List divided relaxed>
                                    <List.Item>
                                        <Icon name='exchange' /> Edge count : {data.length}
                                    </List.Item>
                                    <List.Item>
                                        <Icon name='circle' /> Topic count : {topics.length}
                                    </List.Item>
                                    <List.Item>
                                        <Icon name='certificate' /> Edge types : {type_groups.length}
                                    </List.Item>
                                    <List.Item>
                                        <List>
                                        {type_groups.map((e,i) =>{
                                            return (
                                                <List.Item key={"id"+i}>
                                                    <Label color='yellow' horizontal>{e[0]}</Label>: {e[1].length}
                                                </List.Item>
                                                   
                                            )
                                        })}
                                                
                                        </List>                          
                                    </List.Item>
                                    <List.Item>
                                        <Icon name='chess pawn' /> Topics
                                    </List.Item>
                                    <List.Item>
                                        <Table celled structured size='small'>
                                            <Table.Header>
                                            <Table.Row>
                                                <Table.HeaderCell rowSpan='2'>Topic</Table.HeaderCell>
                                                <Table.HeaderCell rowSpan='2'>Edges</Table.HeaderCell>
                                                <Table.HeaderCell colSpan='4'>Types</Table.HeaderCell>
                                            </Table.Row>
                                            <Table.Row>
                                                <Table.HeaderCell>StC</Table.HeaderCell>
                                                <Table.HeaderCell>StD</Table.HeaderCell>
                                                <Table.HeaderCell>BfC</Table.HeaderCell>
                                                <Table.HeaderCell>BfD</Table.HeaderCell>
                                               
                                            </Table.Row>
                                            </Table.Header>
                                            <Table.Body>
                                                {topic_details.map((e,i)=>{
                                                    return(
                                                        <Table.Row key={"t"+i}>
                                                            <Table.Cell> {e.topic}</Table.Cell>
                                                            <Table.Cell> {e.edges}</Table.Cell>
                                                            <Table.Cell> {(e.type['StC'] === undefined) ? "":e.type['StC'].length}</Table.Cell>
                                                            <Table.Cell> {(e.type['StD'] === undefined) ? "":e.type['StD'].length}</Table.Cell>
                                                            <Table.Cell> {(e.type['BfC'] === undefined) ? "":e.type['BfC'].length}</Table.Cell>
                                                            <Table.Cell> {(e.type['BfD'] === undefined) ? "":e.type['BfD'].length}</Table.Cell>
                                                        </Table.Row>
                                                    )
                                                })}
                                                
                                            </Table.Body>
                                        </Table>
                                        {/* {topic_groups.map((e,i)=>{
                                            return(
                                                // <List.Item key={"id_t"+i}>
                                                //     <Label color='green' horizontal>{e[0]}</Label>: {e[1].length}
                                                // </List.Item> 
                                            )
                                           
                                        })} */}
                                       
                                    </List.Item>
                                    
    
                                </List>
                            
                                
                            </Card.Description> 
                        </Card.Content>
                    </Card>
                </Grid.Column>
            </Grid>
            // <div id={"sankey_"+dealer_id} style={{height:"1100px"}}>
            // </div>
            
            // <Card>
            //     <Card.Content header={"Dealer ID : "+dealer_id+" to Topics"} />
            //      <Card.Content id={"sankey_"+dealer_id} style={{height:"1100px"}}>
            //      </Card.Content>
            //      <Card.Content extra>
            //      <Icon name='exchange' /> Edge count : {data.length}
            //     </Card.Content>
            //  </Card>
        )

    }
}
