import React from "react"

import Layout from "../components/layout/layout"
import {useStaticQuery} from "gatsby"

import { Segment,Tab } from "semantic-ui-react"

import Sankey from "../components/sankey/sankey"


const FractionX10KSankeyPage = ({location}) =>{
  const { state = {} } = location
  const { page_name } = state

  const data = useStaticQuery(graphql`
    query TopDealers_FRAC_Query {
      allTop5DealersFracOutCsv{
        nodes {
          date
          doc_id
          topic
          type
          weight
        }
      }
      dataJson {
        top_dealers_frac {
          dealer_id
        }
      }
    }
  `)
  const dealers = data.dataJson.top_dealers_frac
  const dealers_data = data.allTop5DealersFracOutCsv.nodes

  const panes = dealers.map(dealer =>{
    const dealer_data = dealers_data.filter(function(itm){
      return itm.doc_id === dealer.dealer_id
    })
    return (
      {menuItem: "Dealer: "+dealer.dealer_id ,
      render: () => (
        <Tab.Pane attached={false}>
           <Sankey key={dealer.dealer_id} data={dealer_data} dealer_id={dealer.dealer_id}></Sankey>
        </Tab.Pane>
      )
      
      }
    )
  })

    return(
     <Layout page_name={page_name}>
       <Segment>
         <h2>Sankey : Count | Top 5 Dealers</h2>
       </Segment>

       <Tab menu={{ pointing: true }} panes={panes} />

     </Layout>
    )
  
}

export default FractionX10KSankeyPage;